@import 'ember-uikit';

.fullscreen {
  display: block;
  height: 100vh;
  margin: 0;
  width: 100%;
}

.leaflet-container {
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  transition: width;
  width: 100%;
}

.thumb-nav {
  height: 0;
  width: 100vw;
}

.uk-thumbnav {
  margin-left: 0;

  img {
    height: 10vh;
  }
}

.with-nav {
  .leaflet-container,
  .fullscreen {
    height: 77vh;
  }

  .uk-thumbnav {
    height: 33vh;
  }
}

.uk-offcanvas-content {
  height: 100vh;
}

.uk-offcanvas-bar {
  width: 33vw;
}

.uk-open {
  width: 33vw;
}

@media (min-width: 960px) {
  :not(.uk-offcanvas-flip) > .uk-offcanvas-content-animation {
    left: 33vw;
  }
}

.uk-overlay-default {
  p {
    color: #666;
  }
}
